<template>
  <div>
    <component :is="currentView" :isAdd="isAdd" :modify="isModify" @addNews="addNews" @modifyNews='modifyNews' :formValidate="formValidate" @cancelAdd="cancelAdd"></component>
  </div>
</template>

<script>
import list from './list'
import add from './component/add'
export default {
  components: {
    list,
    add
  },
  data() {
    return {
      currentView: 'list',
      isAdd: false,
      isModify: false,
      formValidate: {}
    }
  },
  methods: {
    addNews({info, flag}){
      // console.log(info, flag)
      this.formValidate = info
      this.isAdd = flag
      this.isModify = !flag
      this.currentView = flag ? 'add' : 'list'
    },
    modifyNews({info, flag}) {
      this.formValidate = info
      this.isAdd = flag
      this.isModify = flag
      this.currentView = flag ? 'add' : 'list'
    },
    cancelAdd() {
      this.isAdd = false
      this.currentView = 'list'
    }
  }
}
</script>




