<template>
  <div>
    <Table :columns="columns" :data="list" :loading="loading" no-data-text="没有满足条件的数据.">
      <Button type="ghost" slot="header" class="m-marlr" @click="addRow">新增</Button>
    </Table>
  </div>
</template>

<script>
  import iView from 'iview'
  export default {
    created() {
      this.loadListData()
    },
    data() {
      return {
        isAdd: false,
        ismodify:false,
        editList: {},
        list: [],
        loading: false,
        columns: [
          {
            title: '标题',
            key: 'title',
            ellipsis: 'true',
            align: 'center'
          },
          {
            title: '版本',
            align: 'center',
            key: 'version'
          },
          {
            title: '操作',
            key: 'action',
            width: 300,
            align: 'center',
            render: (h, params) => {
              const modifyInfo = h('Button', {
                props: {
                  type: 'primary',
                  size: 'small'
                },
                style: {
                  marginRight: '5px'
                },
                on: {
                  click: () => {
                    this.editRow(params)
                  }
                }
              }, '修改');
              const deleteRow = h('Button', {
                props: {
                  type: 'error',
                  size: 'small'
                },
                style: {
                  marginRight: '5px'
                },
                on: {
                  click: () => {
                    this.removeRow(params)
                  }
                }
              }, '删除');
              return h('div', [modifyInfo, deleteRow]);
            }
          }
        ],
        params: {
          pageNumber: 1,
          pageSize: 10
        },
        url: {
          remove: this.projectName+'/mgmt/document/delete.do'
        }
      }
    },
    methods: {
      addRow() {
        this.editList = {
          title: '',
          content: ''
        }
        this.$emit('addNews', {info: this.editList, flag: true})
      },
      editRow(params) {
        // console.log(params)
        const res = params.row
        this.editList = {
          id: res.id,
          title: res.title,
          content: res.content
        }
        this.$emit('modifyNews', {info: this.editList, flag: true})
      },
      removeRow(params) {
        this.$Modal.confirm({
          title: '提示',
          content: '<p>确定删除该条数据么？</p>',
          loading: true,
          onOk: () => {
            this.$ajaxPost(this.url.remove, {
              id: params.row.id
            }).then(response => {
              this.$Modal.remove();
              this.$Message.success('删除成功')
              this.loadListData()
            })
          }
        });
      },
      loadListData(params) {
        this.params = params ? Object.assign({}, this.params, params) : this.params
        this.loading = true
        this.$ajaxPost(this.projectName+'/mgmt/document/finds.do',Object.assign({}, this.params)).then(res => {
          // console.log(res)
          this.loading = false
          this.list = res
        })
      }
    }
  }
</script>

<style>
  .page {
    float: right;
    margin: 20px 0;
  }
  .m-marlr {
    margin: 0 16px;
  }
</style>



