<template>
    <div>
        <div class="top">
            <Button icon="ios-arrow-back" @click="handleReset">返回</Button>
        </div>
        <Form class="form" ref="formValidate" :model="formValidate" :rules="ruleValidate" :label-width="120">
            <FormItem label="标题" prop="title">
                <Input v-model="formValidate.title" placeholder="请输入标题..."></Input>
            </FormItem>
            <FormItem label="内容" prop="content">
                <editor id="editor_id" height="500px" width="100%" :content="formValidate.content"
                    :pluginsPath="'/static/kindeditor/plugins/'"
                    :loadStyleMode="false"
                    :resizeType="1"
                    :wellFormatMode="false"
                    :allowFileManager="true"
                    :uploadJson="projectName+'/editor/file/fileUpload.do?locker=6f0c077ee53d4aa68b9eae5b3e853bb8'"
                    @on-content-change="onEditorChange">
                </editor>
            </FormItem>
        </Form>

        <div class="footer-btn" slot="footer">
            <Button type="primary" @click="handleSubmit('formValidate')">{{!modify?'新增':'修改'}}</Button>
            <Button type="ghost" @click="handleReset" style="margin-left: 8px">取消</Button>
        </div>
    </div>
</template>
<script>
    import moment from 'moment'
    export default {
        props: ['isAdd', 'formValidate', 'modify'],
        data() {
            return {
                tagColors: ['blue', 'green', 'red', 'yellow'],
                options: {
                    classes: []
                },
                ruleValidate: {
                    title: [{
                        required: true,
                        message: '请输入标题...',
                        trigger: 'blur'
                    }],
                    content: {
                        required: true
                    }
                }
            }
        },
        created() {
        },
        methods: {
            onEditorChange(html) {
                this.formValidate.content = html
            },
            addSubmit() {
                this.$ajaxPost(this.projectName + '/mgmt/document/save.do', this.formValidate).then(res => {
                    this.$Message.success('恭喜你，新增成功!');
                    this.$emit('cancelAdd');
                    this.$refs['formValidate'].resetFields();
                    this.$emit('getList');
                });
            },
            modifySubmit() {
                this.$ajaxPost(this.projectName + '/mgmt/document/update.do', this.formValidate).then(res => {
                    this.$Message.success('恭喜你，修改成功!');
                    this.$emit('cancelAdd');
                    this.$refs['formValidate'].resetFields();
                    this.$emit('getList');
                });
            },
            handleSubmit(name) {
                this.$refs[name].validate((valid) => {
                    if (valid) {
                        if (this.modify) {
                            this.modifySubmit();
                        } else {
                            this.addSubmit();
                        }
                    } else {
                        let hit = this.modify ? '修改' : '新增';
                        this.$Message.error(hit + '信息失败，请刷新后重新尝试');
                    }
                })
            },
            handleReset(name) {
                this.$emit('cancelAdd');
                this.$refs['formValidate'].resetFields();
            }
        }
    }
</script>
<style lang="scss" scoped>
    .m-txt-right {
        text-align: center;
    }
    .hint-txt {
        color: red;
        margin-left: 20px;
    }
    .top {
        margin-bottom: 30px;
    }
    .form {
        margin-top: 10px;
    }
    .footer-btn{
        box-sizing: border-box;
        padding: 20px 60px 0;
        border-top: 1px solid #D3D3D3;
    }
</style>



